import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/webinars';

export const apiWebinarGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiWebinarPost = async (serviceUrl, webinar) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: webinar ? JSON.stringify(webinar) : null,
  };
  return request(url, options);
};

export const apiWebinarPut = async (serviceUrl, webinar) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: webinar ? JSON.stringify(webinar) : null,
  };
  return request(url, options);
};

export const apiWebinarDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
